@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Mono";
  font-display: swap;
  src: url("../fonts/PTMono-Regular.woff2") format("woff2"), url("../fonts/PTMono-Regular.woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Placeholder", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  display: block;
  color: #000000;
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

h1,
h2,
h3,
h4,
p {
  margin: 0;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  position: relative;
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.about {
  background: #f8fdff;
  color: #041827;
  padding-bottom: 120px;
}
@media (max-width: 1023px) {
  .about {
    padding-bottom: 80px;
  }
}
@media (max-width: 767px) {
  .about {
    padding-bottom: 75px;
  }
}

.about__wrapper {
  margin-left: 250px;
}
.about__wrapper::before {
  position: absolute;
  content: "";
  width: 150px;
  height: 320px;
  top: 0;
  left: 20px;
  background-image: url("../img/year.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .about__wrapper::before {
    background-image: url("../img/year@2x.png");
  }
}
@media (max-width: 1023px) {
  .about__wrapper::before {
    top: 5px;
    left: 45px;
  }
}
@media (max-width: 767px) {
  .about__wrapper::before {
    display: none;
  }
}
@media (max-width: 1023px) {
  .about__wrapper {
    margin-left: 175px;
  }
}
@media (max-width: 767px) {
  .about__wrapper {
    margin-left: 0;
  }
}

.about__wrapper h2 {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
}
@media (max-width: 767px) {
  .about__wrapper h2 {
    font-size: 28px;
    line-height: 39px;
  }
}

.about__inner {
  display: flex;
  flex-direction: column;
  margin-top: 12px;
  margin-bottom: 20px;
  max-height: 150px;
  overflow: hidden;
}
@media (max-width: 1023px) {
  .about__inner {
    max-height: 260px;
  }
}
@media (max-width: 767px) {
  .about__inner {
    max-height: 210px;
  }
}

.about--more .about__inner {
  max-height: 100%;
  overflow: visible;
}

.about__inner p {
  font-family: "PT Mono", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  margin-bottom: 15px;
}
.about__inner p:last-child {
  margin-bottom: 0;
}
@media (max-width: 1023px) {
  .about__inner p {
    margin-bottom: 20px;
  }
}
@media (max-width: 767px) {
  .about__inner p {
    font-size: 14px;
    line-height: 19px;
  }
}

.about__btn {
  margin-top: 20px;
  padding: 12px 50px;
  border: 1px solid #00b2ff;
  color: #00b2ff;
  text-decoration: none;
}
@media (max-width: 1023px) {
  .about__btn {
    margin-top: 0;
    padding: 13px 50px;
  }
}
@media (max-width: 767px) {
  .about__btn {
    padding: 8px 40px;
  }
}

.about__btn-collapse {
  display: none;
}

.about--more .about__btn-collapse {
  display: block;
}

.about--more .about__btn-more {
  display: none;
}

.features {
  background: #f8fdff;
  padding-top: 120px;
  padding-bottom: 125px;
}
@media (max-width: 1023px) {
  .features {
    padding-top: 80px;
    padding-bottom: 70px;
  }
}
@media (max-width: 767px) {
  .features {
    padding-top: 50px;
    padding-bottom: 15px;
  }
}

.features__list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
}
@media (max-width: 767px) {
  .features__list {
    grid-template-columns: 1fr;
  }
}

.features__item {
  padding: 40px 45px 35px 40px;
  border: 1px solid #ebebeb;
  border-left: 5px solid #00b2ff;
  min-height: 300px;
}
.features__item:hover {
  background-color: #00b2ff;
  cursor: default;
}
.features__item:hover svg {
  fill: #f2f5f6;
}
.features__item:hover h3, .features__item:hover p {
  color: #f2f5f6;
}
@media (max-width: 1023px) {
  .features__item {
    min-height: 360px;
  }
}
@media (max-width: 767px) {
  .features__item {
    padding: 30px 30px 35px 25px;
    min-height: 340px;
  }
}

.features__item svg {
  fill: #00b2ff;
}
@media (max-width: 767px) {
  .features__item svg {
    width: 30px;
    height: 30px;
  }
}

.features__item h3 {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 34px;
  color: #00b2ff;
  margin-top: 25px;
  margin-bottom: 10px;
}
@media (max-width: 1023px) {
  .features__item h3 {
    margin-top: 15px;
    margin-bottom: 5px;
  }
}
@media (max-width: 767px) {
  .features__item h3 {
    margin-bottom: 10px;
    font-size: 20px;
    line-height: 28px;
  }
}

.features__item p {
  font-family: "PT Mono", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #041827;
}
@media (max-width: 767px) {
  .features__item p {
    font-size: 14px;
    line-height: 19px;
  }
}

.btn {
  display: inline-flex;
  padding: 10px 20px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #f2f5f6;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  user-select: none;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
    outline: 1px solid #f2f5f6;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
  outline: 1px solid #f2f5f6;
}
.btn--header {
  padding: 10px 15px;
  border: 1px solid #f2f5f6;
  border-radius: 3px;
  color: #f2f5f6;
  background-color: rgba(255, 255, 255, 0);
  min-height: 40px;
  min-width: 160px;
  max-width: 250px;
}
@media (hover: hover) {
  .btn--header:hover:not(.focus-visible) {
    border-color: #00b2ff;
    color: #00b2ff;
    outline: none;
  }
}
.btn--header.focus-visible:focus {
  border-color: #00b2ff;
  color: #00b2ff;
  outline: none;
}
.btn--hero {
  width: 410px;
  min-height: 55px;
  padding: 15px 25px;
  background-color: #00b2ff;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 21px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}
@media (max-width: 767px) {
  .btn--hero {
    padding: 12px 38px;
    min-width: 270px;
    width: 100%;
    min-height: 40px;
    font-size: 14px;
    line-height: 16px;
    justify-content: center;
  }
}

.contacts {
  position: relative;
}

.contacts__title {
  margin: 0 0 22px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}
@media (max-width: 767px) {
  .contacts__title {
    margin-bottom: 0;
    margin-right: 30px;
  }
}

@media (max-width: 767px) {
  .is-opened .contacts__list {
    display: block;
  }
  .is-closed .contacts__list {
    display: none;
  }
  .is-nojs .contacts__list {
    display: grid;
  }
}
.contacts__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (max-width: 767px) {
  .contacts__list {
    margin-top: 20px;
  }
}

.contacts__item:not(:last-child) {
  margin-bottom: 6px;
}

@media (max-width: 767px) {
  .contacts__item:first-child {
    margin-bottom: 15px;
  }
}

.contacts__address,
.contacts__phone {
  font-family: "PT Mono", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: rgba(255, 255, 255, 0.7);
}

@media (max-width: 767px) {
  .contacts__address {
    font-size: 14px;
    line-height: 19px;
  }
}

@media (max-width: 767px) {
  .contacts__phone {
    font-size: 14px;
    line-height: 25px;
  }
}

.contacts__phone:focus {
  color: #ffffff;
  outline: 1px solid #00b2ff;
  border-radius: 2px;
}

.contacts__phone:hover {
  color: #ffffff;
}

.footer-nav__title {
  margin: 0 0 22px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}
@media (max-width: 767px) {
  .footer-nav__title {
    margin-bottom: 0;
    margin-right: 30px;
    cursor: pointer;
  }
}

.footer-nav {
  position: relative;
}

@media (max-width: 767px) {
  .is-opened .footer-nav__list {
    display: grid;
  }
  .is-closed .footer-nav__list {
    display: none;
  }
  .is-nojs .footer-nav__list {
    display: grid;
  }
}
.footer-nav__nav .footer-nav__list {
  grid-auto-flow: column;
  grid-template-rows: repeat(4, auto);
}
@media (max-width: 1023px) {
  .footer-nav__nav .footer-nav__list {
    grid-auto-flow: row;
  }
}

.footer-nav__list {
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(2, auto);
  gap: 8px 30px;
  list-style: none;
  box-sizing: content-box;
  max-width: 480px;
}
@media (max-width: 1023px) {
  .footer-nav__list {
    grid-template-columns: 1fr;
    grid-auto-flow: row;
    max-width: none;
  }
}
@media (max-width: 767px) {
  .footer-nav__list {
    margin-top: 15px;
  }
}

.footer-nav__item {
  width: 220px;
  font-family: "PT Mono", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
}

.footer-nav__link {
  font-family: "PT Mono", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: rgba(255, 255, 255, 0.7);
}
.footer-nav__link:focus {
  color: #ffffff;
  outline: 1px solid #00b2ff;
  border-radius: 2px;
}
.footer-nav__link:hover {
  color: #ffffff;
}

.footer {
  min-width: 320px;
  background: #041827;
  color: #ffffff;
}

.footer__wrapper {
  display: flex;
  max-width: 1300px;
}
@media (max-width: 1023px) {
  .footer__wrapper {
    display: grid;
    grid-template-columns: repeat(2, auto);
    grid-template-rows: 1fr auto;
    gap: 37px 130px;
    padding: 0 45px 0 50px;
  }
}
@media (max-width: 767px) {
  .footer__wrapper {
    display: flex;
