  align-items: center;
  width: 100%;
  height: 48px;
  padding: 5px 10px;
  background-color: rgba(255, 255, 255, 0);
  border: 2px solid rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  outline: none;
  cursor: pointer;
  transition: border 0.3s ease;
}
.custom-select__button:hover {
  border: 2px solid rgba(0, 0, 0, 0.5);
}
.custom-select__button:focus {
  border: 2px solid rgba(0, 0, 0, 0.8);
}
.custom-select__button:disabled {
  pointer-events: none;
  opacity: 0.3;
}
.custom-select__list {
  position: absolute;
  bottom: 10px;
  z-index: 1;
  width: 100%;
  max-height: 100px;
  margin: 0;
  padding: 0;
  overflow-y: auto;
  list-style: none;
  background-color: #ffffff;
  border: 2px solid #000000;
  transform: translateY(100%);
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease, opacity 0.3s ease;
}
.custom-select__item {
  padding: 5px;
  outline: none;
  cursor: pointer;
  transition: background-color 0.3s ease;
}
.custom-select__item[aria-selected=true] {
  background-color: rgba(0, 0, 0, 0.2);
}
@media (hover: hover) {
  .custom-select__item[aria-selected=true]:hover:not(.focus-visible) {
    background-color: rgba(0, 0, 0, 0.2);
  }
}
.custom-select__item[aria-selected=true].focus-visible:focus {
  background-color: rgba(0, 0, 0, 0.2);
}
@media (hover: hover) {
  .custom-select__item:hover:not(.focus-visible) {
    background-color: rgba(0, 0, 0, 0.4);
  }
}
.custom-select__item.focus-visible:focus {
  background-color: rgba(0, 0, 0, 0.4);
}
.custom-select__error {
  position: absolute;
  bottom: -15px;
  left: 0;
  color: #ff1553;
  opacity: 0;
  transition: opacity 0.1s ease;
}
.custom-select__text {
  font-size: 20px;
  line-height: 24px;
  flex-shrink: 0;
  width: 80%;
  overflow: hidden;
  text-align: left;
  white-space: nowrap;
  text-overflow: ellipsis;
  opacity: 0;
  transition: opacity 0.3s ease 0.1s;
}
.custom-select__text br {
  display: none;
}
.custom-select__icon {
  flex-shrink: 0;
  width: 20px;
  height: 10px;
  margin-left: auto;
  background-color: #000000;
  transition: transform 0.3s ease;
  clip-path: polygon(100% 0, 100% 20%, 50% 100%, 0 20%, 0 0, 50% 80%);
}
.custom-select.is-disabled {
  pointer-events: none;
}
.custom-select.is-disabled .custom-select__list {
  display: none;
}
.custom-select.open-up .custom-select__list {
  bottom: auto;
  top: 0;
  transform: translateY(calc(-100% + 10px));
}
.custom-select.is-open .custom-select__list {
  visibility: visible;
  opacity: 1;
}
.custom-select.is-open .custom-select__icon {
  transform: rotate(180deg);
}
.custom-select.is-open .custom-select__label {
  opacity: 0;
}
.custom-select.not-empty .custom-select__text {
  opacity: 1;
}
.custom-select.not-empty .custom-select__label {
  opacity: 0;
}
.custom-select.not-empty .custom-select__placeholder {
  opacity: 0;
}
.custom-select.is-invalid .custom-select__error {
  opacity: 1;
}
.custom-select.is-invalid .custom-select__button {
  border: 2px solid #ff1553;
}
.custom-select.is-invalid .custom-select__button:focus, .custom-select.is-invalid .custom-select__button:hover {
  border: 2px solid rgba(255, 21, 83, 0.5);
}
.custom-select--placeholder .custom-select__label {
  transform: translateY(calc(-50% - 40px));
  opacity: 1;
}
.custom-select--placeholder.is-open .custom-select__label {
  opacity: 1;
}
.custom-select--placeholder.not-empty .custom-select__label {
  opacity: 1;
}
.custom-select.slide-up.not-empty .custom-select__label, .custom-select.slide-up.is-open .custom-select__label {
  transform: translateY(calc(-50% - 40px));
  opacity: 1;
}
.custom-select.slide-up.not-empty .custom-select__text, .custom-select.slide-up.is-open .custom-select__text {
  opacity: 1;
}
.custom-select .input-message {
  position: absolute;
  right: 0;
  bottom: -5px;
}
.custom-select .input-message.is-invalid {
  color: #ff0000;
}
.custom-select .input-message.is-valid {
  color: #15ab07;
}

.services {
  background-color: #041827;
  color: #ffffff;
  padding-top: 70px;
  padding-bottom: 140px;
}
@media (max-width: 1023px) {
  .services {
    padding-top: 55px;
    padding-bottom: 80px;
  }
}
@media (max-width: 767px) {
  .services {
    padding-top: 45px;
    padding-bottom: 85px;
  }
}

.services h2 {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
  color: #ffffff;
  margin-bottom: 50px;
}
@media (max-width: 1023px) {
  .services h2 {
    margin-bottom: 35px;
  }
}
@media (max-width: 767px) {
  .services h2 {
    margin-bottom: 30px;
    font-size: 28px;
    line-height: 39px;
  }
}

@media (max-width: 767px) {
  .services span {
    display: none;
  }
}

.services b {
  display: none;
}
@media (max-width: 767px) {
  .services b {
    font-weight: 400;
    display: block;
  }
}

.services__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 85px;
  row-gap: 30px;
}
@media (max-width: 1023px) {
  .services__list {
    grid-template-columns: 1fr;
  }
}

.services__item {
  position: relative;
  min-height: 650px;
  background-color: #041827;
  background-image: url("../img/main-block.jpg");
  background-size: 1500px 650px;
  background-repeat: no-repeat;
  object-fit: cover;
  z-index: 1;
  cursor: pointer;
}
.services__item--first {
  background-position: 15% 0;
}
.services__item--second {
  background-position: 47% 0;
}
.services__item--third {
  background-position: 78% 0;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .services__item {
    background-image: url("../img/main-block@2x.jpg");
  }
}
.services__item::after {
  content: "";
  position: absolute;
  background: rgba(4, 24, 39, 0.5);
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: -1;
}
.services__item::before {
  content: "";
  position: absolute;
  bottom: 0;
  right: 0;
  background: #00b2ff;
  background-image: url("../img/svg/arrow.svg");
  background-repeat: no-repeat;
  background-position: center;
  padding: 10px;
  height: 60px;
  min-width: 60px;
}
.services__item:hover, .services__item:focus {
  opacity: 0.8;
  box-shadow: 10px -10px 10px #00b2ff;
}
.services__item:hover::before, .services__item:focus::before {
  border-color: #00b2ff;
}
.services__item:active {
  box-shadow: 0 0 10px #ffffff;
}
@media (max-width: 1023px) {
  .services__item {
    width: 100%;
    min-height: 250px;
    background-size: 1300px 660px;
  }
  .services__item--first {
    background-position: 12% 100%;
  }
  .services__item--second {
    background-position: 80% 60%;
  }
  .services__item--third {
    background-position: 12% 70%;
  }
  .services__item::after {
    min-height: 250px;
    max-width: none;
  }
}
@media (max-width: 767px) {
  .services__item {
    min-width: 270px;
  }
  .services__item--first {
    background-position: 30% 55%;
  }
  .services__item--second {
    background-position: 30% 100%;
  }
  .services__item--third {
    background-position: 75% 73%;
  }
  .services__item::before {
    min-width: 40px;
    height: 40px;
  }
}

.services__link {
  color: #ffffff;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
  padding: 50px 40px;
}
@media (max-width: 1023px) {
  .services__link {
    padding: 38px 45px;
  }
}
@media (max-width: 767px) {
  .services__link {
    padding: 24px 30px;
  }
}

.services__link h3 {
  font-weight: 400;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 20px;
  line-height: 30px;
  width: 180px;
}
@media (max-width: 767px) {
  .services__link h3 {
    font-size: 18px;
    line-height: 30px;
    width: 130px;
  }
}

.social {
  max-width: 215px;
}
@media (max-width: 1023px) {
  .social {
    display: flex;
    max-width: none;
  }
}
@media (max-width: 767px) {
  .social {
    flex-direction: column;
    align-items: center;
  }
}

.social__logo-wrapper {
  margin-bottom: 30px;
}
@media (max-width: 1023px) {
  .social__logo-wrapper {
    margin: 0;
    margin-right: auto;
  }
}
@media (max-width: 767px) {
  .social__logo-wrapper {
    margin: 0 0 15px;
  }
}

.social__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0 0 0 10px;
  padding: 0;
  list-style: none;
}
@media (max-width: 1023px) {
  .social__list {
    padding: 5px 0 0;
  }
}
@media (max-width: 767px) {
  .social__list {
    margin: 0 0 0 4px;
    padding: 0;
    justify-content: center;
  }
}

.social__item {
  display: flex;
  width: 40px;
  height: 40px;
  padding: 5px;
  margin-right: 15px;
}
@media (max-width: 1023px) {
  .social__item:last-child {
    margin: 0;
  }
}

.social__link svg {
  fill: #f8fdff;
}
@media (hover: hover) {
  .social__link svg:hover:not(.focus-visible) {
    opacity: 0.8;
    fill: #00b2ff;
  }
}
.social__link svg.focus-visible:focus {
  opacity: 0.8;
  fill: #00b2ff;
}

.toggle {
  display: none;
}
@media (max-width: 767px) {
  .toggle {
    display: block;
    position: absolute;
    top: 16px;
    right: 22px;
    width: 25px;
    height: 25px;
    box-sizing: border-box;
    outline: none;
    border: none;
    background-color: rgba(255, 255, 255, 0);
  }
}

@media (max-width: 767px) {
  .is-opened .toggle::after {
    content: "";
    position: absolute;
    width: 15px;
    height: 1px;
    background: #c4c4c4;
    top: 11px;
    right: 4px;
  }
  .is-closed .toggle::after {
    content: "";
    position: absolute;
    width: 15px;
    height: 1px;
    background: #c4c4c4;
    top: 11px;
    right: 4px;
  }
  .is-closed .toggle::before {
    content: "";
    position: absolute;
    width: 15px;
    height: 1px;
    background: #c4c4c4;
    top: 11px;
    right: 4px;
    transform: rotate(90deg);
  }
  .is-nojs .toggle {
    display: none;
  }
  .toggle:focus {
    color: #ffffff;
    outline: 1px solid #00b2ff;
    border-radius: 2px;
  }
}
